import axios from "axios"
import MyDate from "./mylibdate"

var Data = {

    async get_data(URL, params = {}) {
        const { data } = await axios.get(URL, {
            params: params,
            headers: {
                'access_token': process.env.VUE_APP_ROOT_APIKEY
            }
        })
        return data
    },

    get_employees() {
        return this.get_data(process.env.VUE_APP_ROOT_API + "employees")
    },

    get_employee_activity_day(dateStart, dateEnd, id_employee) {

        return this.get_data(process.env.VUE_APP_ROOT_API + "activity/"
            + MyDate.convert_to_api_date(dateStart) + "/"
            + MyDate.convert_to_api_date(dateEnd) + "/" 
            + id_employee)
    },

    get_link_download_file(dateStart, dateEnd, ids_employee){
        return process.env.VUE_APP_ROOT_API + "activity/file/"
            + MyDate.convert_to_api_date(dateStart) + "/"
            + MyDate.convert_to_api_date(dateEnd) + "/" 
            + ids_employee
    }


}

export default Data
