<template>
    <div>
        <h3>{{ employee.fio }}</h3>
        <v-data-table :headers="headers" :items="items" :search="search" class="elevation-1" item-key='date' />
    </div>
</template> 

<script>

import Data from '@/mylib/mylibdata'

export default {
    name: "TableData",
    props: {
        dateStart: String,
        dateEnd: String,
        employee: Object
    },
    data: () => ({
        search: '',
        headers: [{
            text: 'Дата',
            value: 'date'
        },
        {
            text: 'Всего время',
            value: 'total_time'
        },
        {
            text: 'Активное время',
            value: 'active_time'
        },
        {
            text: 'В процентах',
            value: 'procent'
        }],
        items: []
    }),
    methods: {
    },

    created() {
        Data.get_employee_activity_day(this.dateStart, this.dateEnd, this.employee.id)
            .then(data => {
                this.items = data
            })

    },



}
</script>

<style scoped>
h3 {
    margin-top: 30px;
    text-align: center;
}
</style>