<template>
  <v-container>

    <v-row>
      <v-col>
        <h1>Рапорт эффективного использования компьютерной техники КБГУ</h1>
      </v-col>
    </v-row>
    <v-spacer></v-spacer>
    <v-row>
      <v-col>
        <v-menu v-model="menu_date_from" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field v-model="date_from" label="Дата от" prepend-icon="mdi-calendar" readonly v-bind="attrs"
              v-on="on">
            </v-text-field>
          </template>
          <v-date-picker v-model="date_from" @input="menu_date_from = false"
            @change="selected_date_action"></v-date-picker>
        </v-menu>
      </v-col>
      <v-col>
        <v-menu v-model="menu_date_to" :nudge-right="40" transition="scale-transition" offset-y min-width="auto">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field v-model="date_to" label="Дата до" prepend-icon="mdi-calendar" readonly v-bind="attrs" v-on="on">
            </v-text-field>
          </template>
          <v-date-picker v-model="date_to" @input="menu_date_to = false" @change="selected_date_action"></v-date-picker>
        </v-menu>
      </v-col>
    </v-row>
    <v-row>
      <v-autocomplete :items="employees" v-model="employees_selected" item-text="fio" return-object label="Сотрудники"
        multiple chips />
    </v-row>
    <v-row>
      <v-col>
        <v-btn block color="primary" @click="downloadXLSX">Скачать XLSX</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <div v-if="employees_selected.length !== 0">
        <div v-for="employee in employees_selected" :key="employee.id">
          <TableData :dateStart="date_from" :dateEnd="date_to" :employee="employee"></TableData>
        </div>
      </div>
    </v-row>
  </v-container>
</template>

<script>
import TableData from '@/components/TableData.vue'
import Data from '@/mylib/mylibdata'
import MyDate from '@/mylib/mylibdate'
import { saveAs } from 'file-saver';

export default {
  name: 'HomeView',

  data: () => ({
    date_from: MyDate.now(),
    date_to: MyDate.now(),
    menu_date_from: false,
    menu_date_to: false,
    employees: [],
    employees_selected: [],
    range_date: []
  }),

  methods: {

    selected_date_action() {
      this.employees_selected = []
    },

    get_ids_from_selected() {
      return this.employees_selected.map((employee) => employee['id'])
    },

    downloadXLSX() {
      saveAs(Data.get_link_download_file(this.date_from, this.date_to, ), 'report_axtivity_' + MyDate.convert_date(MyDate.now()) + '.xlsx')
    },


  },

  mounted() {
    Data.get_employees().then(data => {
      this.employees = data
    }).catch(err => {
      console.log(err)
    })
  },

  components: {
    TableData
  },

  computed: {

  }

}
</script>
